body {
 background-color:#ddd7e0;
 z-index:-1000000;
}
.App {
  text-align: center;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.sketch {
    position:absolute;
    top:20%;
    left:0;
}
.helix-line {
    position:absolute;
    left:0;
    bottom:0;
    z-index:0;
}
.dibujo {
    position:absolute;
    z-index:3;
}
.dibujo-back {
    position:absolute;
    z-index:1;
}
.logo {
    position:absolute;
    bottom:0;
    left:0;
}

.art {
visibility: hidden;
  position: absolute;
  top:20%;
  right:30%;
}
.union {
visibility: hidden;
  position: absolute;
  top:25%;
  right:30%;

}

.marquee {
  margin: 0 auto;
  white-space: nowrap;
  overflow: hidden;
  position: absolute;
  top:20%;
  right:0;
  font-size: 100px;
  z-index:4;
}

@-webkit-keyframes marquee {
  0% {
    -webkit-transform: translate(0, 0);
            transform: translate(0, 0);
  }
  100% {
    -webkit-transform: translate(0, 100%);
            transform: translate(0, 100%);
  }
}

@keyframes marquee {
  0% {
    -webkit-transform: translate(0, 0);
            transform: translate(0, 0);
  }
  100% {
    -webkit-transform: translate(0, 100%);
            transform: translate(0, 100%);
  }
}
.marquee span {
  display: inline-block;
  -webkit-animation: marquee 5s linear infinite;
          animation: marquee 5s linear infinite;
}



